<template>
  <div class="forget-container">
      <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="邮箱" prop="email">
        <el-input
          type="email"
          v-model="ruleForm.email"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item prop="Code">
        <el-input
          type="text"
          v-model="ruleForm.code"
          autocomplete="off"
          class="code"
        ></el-input>
        <el-button class="primary" type="primary" @click="handlerClick" plain
          >获取验证码</el-button
        >
      </el-form-item>
      <el-form-item>
        <el-button class="btn" type="primary" @click="submitForm('ruleForm')">
          确认
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api/login.js';
import Api from '@/api/register.js';
export default {
data() {
    let emailRule = /^[\w]+@[\w.-]+.com$/;
    var validateemail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入邮箱"));
      } else if (emailRule.test(value)) {
        callback();
      } else {
        callback(new Error("邮箱格式不正确"));
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        email: "",
        password: "",
        code: ""
      },
      rules: {
        email: [{ validator: validateemail, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
            api.forget(this.ruleForm).then((res)=> {
            if(res.status === 'success') {
                alert('修改成功，请登录')
                this.$store.dispatch('setUserCookie',{email: this.ruleForm.email})
                this.$router.push({name: '/login'})
            }
      })
          return true;
        } else {
          alert("修改失败");
          return false;
        }
      });
    },
    handlerClick() {
      if(!this.ruleForm.email) {
        return alert('请输入邮箱')
      }
      Api.code({email: this.ruleForm.email}).then((res)=>{
        if(res.status == 'success') {
          alert("验证码发送成功！")
        }
      })
    },
  },
}
</script>

<style scoped lang="less">
.forget-container {
  width: 30%;
  height: 300px;
  margin: 0px auto;
  padding-top: 100px;
  .code {
    width: 50%;
  }
  .primary{
    float: right;
    right: 0;
  }
  .btn{
      width: 100%;
  }
}
</style>